<template>
  <v-card class="pa-4">
    <h2>Outstanding Assignment by Days (Branch)</h2>
    <v-row dense>
      <v-col cols="12" md="4">
        <v-select
          v-model="selectedDept"
          :items="deptOptions"
          item-text="name"
          item-value="id"
          label="Department"
          dense
          outlined
          required
        />
      </v-col>
      <v-col cols="12" md="4">
        <v-menu v-model="menuMonth" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="selectedMonth"
              label="Month"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              required
            />
          </template>
          <v-date-picker
            v-model="selectedMonth"
            type="month"
            @change="menuMonth = false"
          />
        </v-menu>
      </v-col>
      <v-col cols="12" md="4" class="d-flex align-end">
        <v-btn
          color="primary"
          @click="generateReport"
          :disabled="!selectedMonth"
        >
          Generate Report
        </v-btn>
      </v-col>
    </v-row>
    <v-divider class="my-4" />
    <div v-if="reportData.length">
      <v-simple-table>
        <thead>
          <tr>
            <th>Branch</th>
            <th>Outstanding Below 30 Days</th>
            <th>Outstanding Above 30 Days</th>
            <th>Outstanding Above 45 Days</th>
            <th>Outstanding Above 60 Days</th>
            <th>Outstanding Above 90 Days</th>
            <th>Total</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="row in reportData" :key="row.branch">
            <td>
              <strong>{{ row.branch }}</strong>
            </td>
            <td>{{ row.below30 }}</td>
            <td>{{ row.above30 }}</td>
            <td>{{ row.above45 }}</td>
            <td>{{ row.above60 }}</td>
            <td>{{ row.above90 }}</td>
            <td>{{ row.total }}</td>
          </tr>
          <tr>
            <td><strong>TOTAL</strong></td>
            <td>
              <strong>{{ totals.below30 }}</strong>
            </td>
            <td>
              <strong>{{ totals.above30 }}</strong>
            </td>
            <td>
              <strong>{{ totals.above45 }}</strong>
            </td>
            <td>
              <strong>{{ totals.above60 }}</strong>
            </td>
            <td>
              <strong>{{ totals.above90 }}</strong>
            </td>
            <td>
              <strong>{{ totals.total }}</strong>
            </td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td><strong>NEW FILES</strong></td>
            <td></td>
            <td></td>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td><strong>GRAND TOTAL</strong></td>
            <td></td>
            <td>
              <strong>{{ totals.total }}</strong>
            </td>
          </tr>
        </tbody>
      </v-simple-table>
      <v-btn
        color="success"
        class="mt-4"
        @click="exportExcel"
        :loading="exporting"
      >
        <v-icon left>mdi-file-excel</v-icon>
        Export to Excel
      </v-btn>
    </div>
    <v-alert v-else-if="noDataMessage" type="warning" outlined class="mt-4">
      {{ noDataMessage }}
    </v-alert>
    <v-alert v-else type="info" outlined class="mt-4">
      Please select department and month, then click "Generate Report" to view
      data.
    </v-alert>
  </v-card>
</template>

<script>
import axios from "axios";
import moment from "moment";
import DeptDataService from "../../services/DeptDataService";
import { apiConfig } from "../../config/api.config";

export default {
  name: "OutstandingDaysBranch",
  data() {
    return {
      selectedDept: "all",
      deptOptions: [],
      selectedMonth: null,
      menuMonth: false,
      reportData: [],
      totals: {
        below30: 0,
        above30: 0,
        above45: 0,
        above60: 0,
        above90: 0,
        total: 0,
      },
      exporting: false,
      noDataMessage: "",
    };
  },
  methods: {
    async loadDepartments() {
      const res = await DeptDataService.getAll();
      this.deptOptions = [{ id: "all", name: "ALL DEPARTMENTS" }, ...res.data];
    },
    async generateReport() {
      this.reportData = [];
      this.totals = {
        below30: 0,
        above30: 0,
        above45: 0,
        above60: 0,
        above90: 0,
        total: 0,
      };
      this.noDataMessage = "";
      try {
        const params = {
          deptId: this.selectedDept === "all" ? null : this.selectedDept,
          month: moment(this.selectedMonth).format("YYYY-MM"),
        };
        const res = await axios.get(
          `${apiConfig.baseURL}/compliance/outstanding-days-branch`,
          { params }
        );
        this.processReportData(res.data);
        if (
          !Array.isArray(res.data.reportData) ||
          res.data.reportData.length === 0
        ) {
          const monthStr = moment(this.selectedMonth).format("MMMM YYYY");
          this.noDataMessage = `No outstanding data found for ${monthStr}.`;
        }
      } catch (err) {
        this.noDataMessage =
          err.message || "Failed to fetch report data. Please try again.";
      }
    },
    processReportData(data) {
      this.reportData = Array.isArray(data.reportData) ? data.reportData : [];
      const totals = {
        below30: 0,
        above30: 0,
        above45: 0,
        above60: 0,
        above90: 0,
        total: 0,
      };
      this.reportData.forEach((row) => {
        totals.below30 += row.below30;
        totals.above30 += row.above30;
        totals.above45 += row.above45;
        totals.above60 += row.above60;
        totals.above90 += row.above90;
        totals.total += row.total;
      });
      this.totals = totals;
    },
    async exportExcel() {
      this.exporting = true;
      try {
        const params = {
          deptId: this.selectedDept === "all" ? "" : this.selectedDept,
          month: moment(this.selectedMonth).format("YYYY-MM"),
        };
        const url =
          `${apiConfig.baseURL}/compliance/outstanding-days-branch/export?` +
          new URLSearchParams(params).toString();
        window.open(url, "_blank");
      } catch (err) {
        this.$emit("error", err.message || "Failed to export Excel");
      } finally {
        this.exporting = false;
      }
    },
  },
  mounted() {
    this.loadDepartments();
  },
};
</script>
